@import '00_common/01_scss/variables';

@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    box-sizing: border-box;
}

/*
//=======================================================================
// REACT-DRAGGABLE
//
// moving container at the TOP of ALL containers

.react-draggable-dragging {
    z-index: 1000;
}
//=======================================================================
*/

::-webkit-scrollbar {
    background: transparent;
    width: $scrollSize;
    height: $scrollSize;
    position: absolute;
}

::-webkit-scrollbar-track {
    background: transparent;
    position: absolute;
    z-index: -2;
    background: rgba(189, 189, 189, 0.3);
}

::-webkit-scrollbar-thumb {
    background: #5c5c5c;
}

::-webkit-scrollbar-thumb:hover {
    background: #000
}


body {
    margin: 0;

    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
